	export default {
		props: {
			columns: {
				type: Array,
				required: true
			},
			list: {
				type: Array,
				required: true
			},
			rowClassName:{
				type:[String,Function],
				default:""
			},
			'slot-cols':{
				type:Array,
				default:()=>{
					return []
				}
			},
			"span-method":{
				type:Function,
				default:()=>{
					return ()=>{
						return {
							rowspan:1,
							colspan:1
						}
					}
				}
			},
			//是否可选  mulit=>多选   single=》单选
			selection:{
				type:String,
				default:"none"
			},
			height:{
				type:Number,
				default:undefined
			},
			"td-width": {
				type: Number,
				default: 110
			},
			"td-height": {
				type: Number,
				default: 30
			},
			"td-padding": {
				type: Number,
				default: 10
			},
			"border-color": {
				type: String,
				default: "#666"
			}
		},
		computed:{
			tableHeight(){
				return Number(this.height)&&Number(this.height)>this.tdHeight*3?this.height+"px":"auto";
			},
			talbeBodyHeight(){
				let t=this.tableHeight!=="auto"?(parseInt(this.tableHeight)-this.tdHeight-1)+"px":"auto";
				return t;
			},
			allCheckBoxAbledLen(){
				return this.checkBoxList.filter(item=>!item.$disabled).length;
			}
		},
		data(){
			return {
				checkBoxList:[],
				switchAllCheckBox:false,
				selectionTdWidth:"50px",
				singleSelect:{}
			}
		},
		watch:{
			"list"(){
				this.asyncCheckBoxList();
			}	
		},
		created() {
			this.asyncCheckBoxList();
		},
		methods:{
			asyncCheckBoxList(){
				this.checkBoxList=this.list.map(item=>{
					return {...item};
				});
			},
			rowClassNamePlus(row,index){
				if(typeof this.rowClassName==="string"){
						return this.rowClassName;
				}
				else if(typeof this.rowClassName==="function"){
					return this.rowClassName(row,index);
				}
			},
			pullEvent(event,data){
					this.$emit(event,data);
			},
			countColspanWidth(item,tdItem,index,tdItemIndex){
				return this.spanMethod(item,tdItem,index,tdItemIndex)&&this.spanMethod(item,tdItem,index,tdItemIndex)["colspan"]>1?(this.spanMethod(item,tdItem,index,tdItemIndex)["colspan"]*this.tdWidth)+"px":this.tdWidth+"px";
				         
			},
			countRowspanHeight(item,tdItem,index,tdItemIndex){
				return this.spanMethod(item,tdItem,index,tdItemIndex)&&this.spanMethod(item,tdItem,index,tdItemIndex)["rowspan"]>1?(this.spanMethod(item,tdItem,index,tdItemIndex)["rowspan"]*this.tdHeight)+"px":this.tdHeight+"px"
			},
			selectRow(item,index){
				if(item.$disabled){
					return;
				}
				this.checkBoxList=this.checkBoxList.map((sitem,sindex)=>{
					if(index===sindex){
						sitem.$checked=true
					}
					else{
						sitem.$checked=false;
					}
					return sitem;
				});
				if(this.selection){
					this.$emit("on-selection-change",{
						old:this.singleSelect,
						new:{index,item}
					})
				}
				this.singleSelect={index,item};
				
			},
			checkboxChange(e){
				let val = e.detail.value;
				let before=[];
				for(let v=0;v<this.allCheckBoxAbledLen;v++){
					if(this.checkBoxList[v].$checked===true){
						before.push({...this.checkBoxList[v]});
					}
				}
                if(val.length==this.allCheckBoxAbledLen){
					this.switchAllCheckBox=true;
					this.checkBoxList=this.checkBoxList.map(item=>{
						if(!item.$disabled){
							item.$checked=true
						}
						return item;
					});
				}
				else{
					this.switchAllCheckBox=false;
					this.checkBoxList=this.checkBoxList.map(item=>{
						if(val.indexOf(item.id)>-1){
							item.$checked=true
						}
						else{
							item.$checked=false;
						}
						return item;
					});
				}
				this.$emit("on-selection-change",{
					old:before,
					new:this.checkBoxList.filter(item=>item.$checked===true)
				})
			},
			checkboxChangeAll(e){
				let val=e.detail.value;
				let before=[];
				for(let v=0;v<this.allCheckBoxAbledLen;v++){
					if(this.checkBoxList[v].$checked===true){
						before.push({...this.checkBoxList[v]});
					}
				}
				if(val&&val[0]=="all"){
					this.switchAllCheckBox=true;
					this.checkBoxList=this.checkBoxList.map(item=>{
						if(!item.$disabled){
							item.$checked=true
						}
						return item;
					});
				}
				else{
					this.switchAllCheckBox=false;
					this.checkBoxList=this.checkBoxList.map(item=>{
						item.$checked=false
						return item;
					});
				}
				this.$emit("on-selection-change",{
					old:before,
					new:this.checkBoxList.filter(item=>item.$checked===true)
				})
			}
		}
	}
